.slider{
    position: absolute;
    width: 50%;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}


.slider-image{
    /*position: absolute;*/
    width: 50%;
    height: 120px;
    transition: all 0.05s;
}

.slider-image-prev{
    transform: translateX(-50%);
}

.slider-image-next{
    transform: translateX(50%);
}